@import url(https://fonts.googleapis.com/css2?family=Syncopate&display=swap);
/* Position and sizing of burger button */
.bm-burger-button {
  position: fixed;
  width: 36px;
  height: 30px;
  left: 30px;
  top: 30px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
  background: white;
  transition: 0.5s ease-in-out;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
  background: #00ffbf;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
  height: 24px;
  width: 24px;
}

/* Color/shape of close button cross */
.bm-cross {
  background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
  position: fixed;
  height: 100%;
}

/* General sidebar styles */
.bm-menu {
  background: black;
  padding: 2.5em 1.5em 0;
  font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
  fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list {
  color: #b8b7ad;
  padding: 0.8em;
}

/* Individual item */
.bm-item {
  display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
  background: rgba(0, 0, 0, 0.3);
}

.link {
  color: cyan;
  transition: 0.5s ease-in-out;
}

.link:hover {
  text-decoration: none;
  color: #00ffbf;
}

.link-white {
  color: white;
}

.link-white:hover {
  text-decoration: none;
  color: white;
}

.font-em {
  font-size: 1.2rem;
}

.email-link {
  color: whitesmoke;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.email-link:hover {
  text-decoration: none;
  color: #00ffbf;
}

.discord-link {
  color: whitesmoke;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.discord-link:hover {
  color: #7289da;
  text-decoration: none;
}

.typed {
  font-family: "Syncopate", sans-serif;
  background-color: black;
  color: cyan;
}

.section-title {
  font-family: "Syncopate", sans-serif;
  color: cyan;
  transition: 0.5s ease-in-out;
}
.section-title:hover {
  color: #00ffbf;
}

.curved-div {
  font-size: 40px;
  text-align: center;
  border-radius: 4px;
  padding-bottom: 20px;
  background-color: #343a40;
  transition: 0.5s ease-in-out;
}
.curved-div:hover {
  background-color: #121416;
  border-bottom-left-radius: 100%;
  border-bottom-right-radius: 100%;
}

.form-card {
  background-color: black;
  border: 1px solid black;
  color: white;
  transition: 0.5s ease-in-out;
}
.form-card:hover {
  border: 1px solid white;
  border-radius: 10px;
}

.Progress {
  background-color: gray;
  transition: 0.5s ease-in-out;
  text-align: center;
  margin: 10px auto;
  width: 80vw;
}
.Progress:hover {
  background-color: cadetblue;
}
.ProgressBar {
  transition: 0.5s ease-in-out;
  background-color: cyan;
  font-weight: bold;
  height: 25px;
}
.ProgressBar:hover {
  background-color: #00ffbf;
}

body::-webkit-scrollbar {
  width: 0.5em;
  background-color: black;
}

body::-webkit-scrollbar-thumb {
  background-color: #00ffbf;
  border-radius: 10px;
}

html {
  -webkit-scroll-snap-type: y none;
      -ms-scroll-snap-type: y none;
          scroll-snap-type: y none;
  overflow-x: hidden;
}

body {
  background-color: black;
}

.scroll-child {
  scroll-snap-align: start;
  min-height: 100vh;
}

.scroll-x {
  overflow-x: auto;
}

@media (hover: hover) {
  html {
    -webkit-scroll-snap-type: y proximity;
        -ms-scroll-snap-type: y proximity;
            scroll-snap-type: y proximity;
    overflow-x: hidden;
  }
  .scroll-child {
    scroll-snap-align: start;
    min-height: 100vh;
  }
}

